import React from 'react'
import Card from 'react-bootstrap/Card'

export default function Cardgenerator(props) {
    return (
        <Card className={props.cardClass} border='info'>
            <Card.Img variant="top" src={props.img} />
            <Card.Body>
                <Card.Title>
                    {props.title}
                </Card.Title>
                <Card.Text>
                   {props.text}
                </Card.Text>
                {props.atagValid && <a href={props.link}>EXPLORE</a>}
                
            </Card.Body>
      </Card>
    )
}
